<template>
  <div class="medicalprint">
    <div>
      <!-- 畫面顯示內容區域 -->
      <h3>{{cData.company_name}}</h3>

      <div class="text-center border-bottom my-2">
        <h1>
          <strong>就診紀錄單</strong>
        </h1>
        <h4 class="text-left font-weight-black">就診日期: {{list.date}}</h4>
      </div>

      <table class="form_title" style="border:2px #000000 solid;" width="100%">
        <tr>
          <td>
            <v-row class="px-2">
              <v-col cols="3">姓名: {{uData.name}} </v-col>
              <v-col cols="2">床號: {{uData.bed_name}} </v-col>
              <v-col cols="3">身分證字號: {{uData.id_num}}</v-col>
              <v-col cols="2">性別: {{uData.sex}}</v-col>
              <v-col cols="2">血型: {{uData.blood}}</v-col>
              <v-col cols="3">年齡: {{age}}</v-col>
              <v-col cols="3">生日: {{uData.birthday}}</v-col>
              <v-col cols="3">身高: {{uData.height}}</v-col>
              <v-col cols="3">體重: {{uData.weight}}</v-col>
              <v-col cols="6">藥物過敏: {{uData.allergyhistory2}}</v-col>
              <v-col cols="6">疾病診斷: {{uData.diagnosis | filterdiagnosis}}</v-col>
              <v-col cols="6">過去病史: {{uData.hadadisease}}</v-col>
            </v-row>
          </td>
        </tr>
        <tr>
          <td>
            <v-row class="px-2">
              <!-- <v-col cols="4">住址: {{uData.address}}</v-col> -->
              <v-col cols="8">
                管路(到期日):
                <v-chip
                  small
                  color="grey lighten-3"
                  v-for="(plist,index) in matchPipelineItems"
                  :key="index"
                >{{plist.name}}({{plist.end_date}})</v-chip>
              </v-col>
              <v-col cols="4">攜帶物品: {{list.carryitems}}</v-col>
            </v-row>
          </td>
        </tr>

        <tr>
          <td>
            <v-row class="px-2">
              <v-col cols="2">診別: {{list.outpatient}}</v-col>
              <v-col cols="2">時段: {{list.time}}</v-col>
              <v-col cols="4">醫院: {{list.hospital_name}}</v-col>
              <v-col cols="3">科別: {{list.branch_name}}</v-col>
              <v-col cols="2">醫生: {{list.doctor_name}}</v-col>
              <v-col cols="4">使用車輛: {{list.car}}</v-col>
              <v-col cols="3">陪診人員: {{list.accompany}}</v-col>
            </v-row>
          </td>
        </tr>

        <tr>
          <td class="px-2">
            <span class="text-left font-weight-black">當前服用藥物:</span>
            <!-- <div v-for="(item,index) in medicineData" :key="index">
              <div v-if="reprint(index, medicineData)">{{item.medical.hospital_name}}-[{{item.medical.branch_name}}]</div>
              <div>
                [{{index+1}}.] {{item.name}} / {{item.dosage}} / {{item.method_name}} / 每次{{item.quantity}}{{item.unit_name}} / {{item.eat_medicine_time.name}}
              </div>
            </div>-->
            <v-row v-for="(item,index) in medicineData" :key="index" class="mx-0">
              <v-col
                cols="12"
                class="border-bottom border-top grey lighten-2"
                v-if="reprint(index, medicineData)"
              >【{{item.medical.hospital_name}}】-[{{item.medical.branch_name}}]</v-col>
              <v-col
                cols="12"
              >[{{index+1}}.] {{item.name}} / {{item.dosage}} / {{item.method_name}} / 每次{{item.quantity}}{{item.unit_name}} / {{item.eat_medicine_time.name}} / 最後用藥: {{item.medicineEndInfo}}</v-col>
            </v-row>
          </td>
        </tr>

        <tr>
          <td>
            <v-row class="px-2">
              <v-col cols="12">
                <span
                  class="text-left font-weight-black"
                >本次就診主因: {{list.main_cause_of_treatment_name}}</span>
              </v-col>
              <v-col cols="12">
                <span class="text-left font-weight-black">原因狀況描述:</span>
                <br />
                {{list.description}}
              </v-col>
              <v-col cols="4">
                <v-icon large>mdi-border-all-variant</v-icon>不實施心肺復甦術(DNR)
              </v-col>
              <v-col cols="5">本機構電話 {{cData.tel}} 轉接護理人員</v-col>
              <v-col cols="3">護理人員簽名: {{list.create_man | filterName}}</v-col>
            </v-row>
          </td>
        </tr>

        <tr>
          <td>
            <v-row class="px-2">
              <v-col cols="12" class="mb-3">
                <span class="text-left font-weight-black">請醫情形回覆:</span>
                <br />
                <br />
                <br />
                <br />
                <br />
                <br />
                <br />
                {{list.response}}
              </v-col>
              <v-col cols="9">
                <v-icon large>mdi-border-all-variant</v-icon>告知家屬:
              </v-col>
              <v-col cols="3">醫師簽名:</v-col>
            </v-row>
          </td>
        </tr>
      </table>

      <h4 class="mt-3 font-weight-black border-bottom">
        生命徵象紀錄
        ( {{startDay}} ~ {{endDay}} )
      </h4>

      <table class="form_title" style="border:2px #000000 solid;" width="100%">
        <tr>
          <td v-if="printSet.date">評估日期</td>
          <td v-if="printSet.time">評估時間</td>
          <td v-if="printSet.result">檢查結果</td>
          <td v-if="printSet.weight">體重</td>
          <td v-if="printSet.bp_h">血壓(H)</td>
          <td v-if="printSet.bp_l">血壓(L)</td>
          <td v-if="printSet.pulse">脈搏</td>
          <td v-if="printSet.temperature">體溫</td>
          <td v-if="printSet.breath">呼吸</td>
          <td v-if="printSet.blood_sugar">血糖</td>
          <td v-if="printSet.blood_oxygen">血氧濃度</td>
          <td v-if="printSet.pain">疼痛指數</td>
          <td v-if="printSet.gcs_e">GCS-E</td>
          <td v-if="printSet.gcs_v">GCS-V</td>
          <td v-if="printSet.gcs_m">GCS-M</td>
          <td v-if="printSet.stool">大便次數</td>
          <td v-if="printSet.note">備註</td>
        </tr>

        <tr v-for="(item,index) in signlifeData" :key="index">
          <td v-if="printSet.date">{{item.date}}</td>
          <td v-if="printSet.time">{{item.time}}</td>
          <td v-if="printSet.result">{{item.result}}</td>
          <td v-if="printSet.weight">{{item.weight}}</td>
          <td v-if="printSet.bp_h">{{item.bp_h}}</td>
          <td v-if="printSet.bp_l">{{item.bp_l}}</td>
          <td v-if="printSet.pulse">{{item.pulse}}</td>
          <td v-if="printSet.temperature">{{item.temperature}}</td>
          <td v-if="printSet.breath">{{item.breath}}</td>
          <td v-if="printSet.blood_sugar">{{item.blood_sugar}}</td>
          <td v-if="printSet.blood_oxygen">{{item.blood_oxygen}}</td>
          <td v-if="printSet.pain">{{item.pain}}</td>
          <td v-if="printSet.gcs_e">{{item.gcs_e}}</td>
          <td v-if="printSet.gcs_v">{{item.gcs_v}}</td>
          <td v-if="printSet.gcs_m">{{item.gcs_m}}</td>
          <td v-if="printSet.stool">{{item.stool}}</td>
          <td v-if="printSet.note">{{item.note}}</td>
        </tr>
      </table>

      <!-- <pre>{{signlifeData}}</pre> -->
    </div>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  data() {
    return {
      cData: {},
      pData: {},
      uData: {},
      list: {}, //表單內資料
      medicineData: [], //目前使用藥物暫存
      signlifeData: [], //生命徵象資料
      printSet: [], //列印設定
      startDay: "",
      endDay: "",
      // nursingassessmentItems: {},
      // nutritionItems: [],
      // recordItems: [],
      matchPipelineItems: [],
    };
  },

  filters: {
    filterName(val) {
      if (val != undefined) {
        var name = val.split("(");
        return name[0];
      }
    },
    filterdiagnosis(val) {
      if (val != undefined) {
        var str = Object.values(val);
        return str.toString();
      }
    },
  },

  computed: {
    age() {
      return dayjs().format("YYYY") - dayjs(this.uData.birthday).format("YYYY");
    },
    ibw() {
      //IBW = 身高(公尺)*身高(公尺)*22，例如1.7*1.7*22=63.58
      var ibw = (this.uData.height / 100) * (this.uData.height / 100) * 22;
      return ibw.toFixed(2);
    },
  },

  mounted() {
    this.cData = JSON.parse(sessionStorage.getItem("cData"));
    this.pData = JSON.parse(sessionStorage.getItem("pData"));
    this.uData = JSON.parse(sessionStorage.getItem("uData"));
    // this.list = JSON.parse(sessionStorage.getItem("printData"))["medicalData"];
    // this.medicineData = JSON.parse(sessionStorage.getItem("printData"))[
    //   "medicineData"
    // ];
    // this.signlifeData = JSON.parse(sessionStorage.getItem("printData"))[
    //   "signlifeData"
    // ];
    // this.printSet = JSON.parse(sessionStorage.getItem("printData"))["printSet"][
    //   "signlife"
    // ];

    var printItems = JSON.parse(sessionStorage.getItem("printData"));
    console.log(printItems);
    this.list = printItems.medicalData;
    this.medicineData = printItems.medicineData;
    this.signlifeData = printItems.signlifeData;
    this.printSet = printItems.printSet.signlife;
    this.startDay = printItems.printSet.startDay;
    this.endDay = printItems.printSet.endDay;

    this.getPipeline();
    // this.getNursingassessment(); //取得入住護理評估(本病歷的)
    // this.getNutrition(); //取得營養評估單(本病歷的)
    // this.getRecord();
  },

  methods: {
    getPipeline() {
      var postData = {
        key: "u_snkey",
        value: this.list.user_snkey,
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/pipeline_used",
          qs.stringify(postData)
        )
        .then((rs) => {
          var pipelineData = rs.data.map((i) => {
            var newItem = JSON.parse(i.datalist);
            newItem["snkey"] = i.snkey;
            newItem["create_man"] = i.create_man;
            newItem["edit_man"] = i.edit_man;
            return newItem;
          });
          this.matchPipelineItems = pipelineData.filter((i) => {
            return (
              dayjs(i.end_date).format("YYYY-MM-DD") >
              dayjs().format("YYYY-MM-DD")
            );
          });

          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    //判斷是否顯示醫院名
    reprint(index, items) {
      if (index == 0) {
        return true;
      } else {
        if (items[index].medical.snkey == items[index - 1].medical.snkey) {
          return false;
        } else {
          return true;
        }
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@import "node_modules/bootstrap/scss/bootstrap";
@import "node_modules/bootstrap-vue/src/index.scss";

.medicalprint {
  .form_title {
    font-size: 1.2rem;
    // text-align: center;
    font-family: "微軟正黑體";

    td {
      border: 1px #000000 solid;

      .col {
        padding-top: 2px;
        padding-bottom: 2px;
      }
    }
  }

  .v-chip {
    font-size: 1rem;
  }
}

@media print {
  .medicalprint {
  .form_title {
    font-size: 1.2rem;
    // text-align: center;
    font-family: "微軟正黑體";

    td {
      border: 1px #000000 solid;

      .col {
        padding-top: 2px;
        padding-bottom: 2px;
      }
    }
  }

  .v-chip {
    font-size: 1rem;
  }

  @page {
      size: A4 portrait;
      margin: 0cm;
      padding:0cm;
    }
}
}
</style>